.MemoryCard {
    display: inline-block;
    width: 250px;
    height: 150px;
    margin: 10px;
    cursor: pointer;
    position: relative;
    -webkit-perspective: 800px;
            perspective: 800px;
}

.MemoryCardInner {
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: -webkit-transform 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.5);
    transition: -webkit-transform 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.5);
    -o-transition: transform 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.5);
    transition: transform 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.5);
    transition: transform 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.5), -webkit-transform 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.5);
    border-radius: 5px;
    border: 3px solid navy;
    -webkit-box-shadow: 0 6px 16px rgba(0,0,0,0.15);
            box-shadow: 0 6px 16px rgba(0,0,0,0.15);
}

.flipped { -webkit-transform: rotateY(180deg); transform: rotateY(180deg); }

.MemoryCardFront, .MemoryCardBack {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.MemoryCardBack {
    background: -webkit-repeating-linear-gradient(
        45deg,
        rgb(68, 92, 128),
        rgb(68, 92, 128) 10px,
        #9095af 10px,
        #9095af 20px
    );
    background: -o-repeating-linear-gradient(
        45deg,
        rgb(68, 92, 128),
        rgb(68, 92, 128) 10px,
        #9095af 10px,
        #9095af 20px
    );
    background: repeating-linear-gradient(
        45deg,
        rgb(68, 92, 128),
        rgb(68, 92, 128) 10px,
        #9095af 10px,
        #9095af 20px
    );
}

.MemoryCardFront img {
    width: 100%;
    height: 100%;
    
}

.MemoryCardFront {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.MemoryCardBack img {
    width: 100px;
    padding-top: 27px;
}


